
* {
    margin: 0;
    padding: 0;
}
.start {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.star,
.star2 {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    font-size: 2em;
    /* margin-bottom: 5px; */
}
.comment-main {
    visibility: hidden;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 400px;
    z-index: 100;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -webkit-perspective: 1000px;
            perspective: 1000px;
}
.container {
    /* display:flex; */
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}


/* .comment-main {
    visibility: hidden;
    margin:auto;
    width:400px;
    z-index: 100;
    perspective: 1000px;
} */
.main {
    opacity: 0;
    -webkit-transform: rotateY(-70deg);
            transform: rotateY(-70deg);
    /* rotateY和translateX会导致另一个失效 */
    background: white;
    border-radius: 10px;
    -webkit-transition: 0.6s;
    transition: 0.6s;
}
.showComment .main {
    opacity: 1;
    -webkit-transform: rotateY(0);
            transform: rotateY(0);
}
.showComment {
    visibility: visible;
    /* z-index:100; */
}
header {
    /* margin: 40px; */
    background: #0090d3;
    padding: 10px;
    color: white;
    border-radius: 0 0 5px 5px;
    margin-bottom: 10px;
}
.content {
    /* background: white; */
    margin: 15px 0 10px 0;
    /* padding:20px; */
}
.showComment~.overlay {
    opacity: 1;
    visibility: visible;
}
.overlay {
    -webkit-transition: 0.7s;
    transition: 0.7s;
    opacity: 0;
    visibility: hidden;
    background: rgba(0, 0, 0, 0.6);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 99;
}
.basic {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    /* font-size: 1em; */
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
li.factor {
    display: inline-block;
    border: 1px solid rgba(0, 0, 0, 0.3);
    color: rgba(0, 0, 0, 0.6);
    padding: 5px 10px;
    margin: 0 10px 10px 10px;
    font-size: 0.9em;
    border-radius: 5px;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}
li.factor.focused {
    color: #0090D3;
    border-color: #0090d3;
}
.factor:hover {
    cursor: pointer;
    /* 光标 */
    opacity: 0.9;
    -webkit-box-shadow: 0 2px 2px 0 rgba(181, 50, 85, 0.3);
            box-shadow: 0 2px 2px 0 rgba(181, 50, 85, 0.3);
}
footer textarea {
    width: 80%;
    height: 80px;
    font-size: 1.1em;
    color: rgba(0, 0, 0, 0.6);
    resize: none;
    /* overflow-y: hidden; */
    outline-color: #0090d3;
    padding: 5px;
    /* margin-top: 20px; */
}
.start .myBnt {
    padding: 7px 10px;
    font-size: 2em;
    width: 60px;
}
.myBnt {
    margin: 20px;
    background-color: #0090d3;
    border: none;
    border-radius: 7px;
    padding: 10px 0;
    font-size: 20px;
    outline: none;
    color: white;
    width: 60px;
    -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
    -webkit-transition: 0.5s;
    transition: 0.5s;
}
.myBnt:active {
    /* transition: 1s; */
    background-color: #0e5c83;
}
.myBnt:hover {
    /* padding-right: 10px; */
    cursor: pointer;
    /* 光标 */
    opacity: 0.9;
    -webkit-box-shadow: 0 7px 12px 0 rgba(0, 0, 0, 0.5);
            box-shadow: 0 7px 12px 0 rgba(0, 0, 0, 0.5);
}
@media (max-width:500px) {
.comment-main {
        width: 80vw;
        margin-top: 10px;
}
footer .myBnt {
        padding: 5px 5px;
        /* font-size: 0.8em; */
        width: 60px;
        margin: 7px 20px
}
footer textarea {
        height: 10vw;
        min-height: 40px;
        /* font-size: 0.9em; */
}
footer .mybnt,
    footer textarea {
        font-size: 0.9em
}
header h2 {
        font-size: 1.3em;
        padding: 0;
        margin: 0;
}
header {
        padding: 5px
}
li.factor,
    .content {
        margin: 5px;
}
.star {
        margin-top: -10px;
}
}



